$myfonts: elektra_light_pro-webfont, elektra_medium_pro_italic-webfont, elektra_medium_pro-webfont, font-webfont, opensans-regular-webfont, opensans-semibold;

@each $item in $myfonts {
	@font-face {
	    font-family: #{$item};
	    src: url('../fonts/#{$item}.eot');
	    src: url('../fonts/#{$item}.eot?#iefix') format('embedded-opentype'),
	         url('../fonts/#{$item}.woff2') format('woff2'),
	         url('../fonts/#{$item}.woff') format('woff'),
	         url('../fonts/#{$item}.ttf') format('truetype'),
	          url('../fonts/#{$item}.svg') format('svg');
	    font-weight: normal;
	    font-style: normal;
	}
}
